@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@400;600&family=Montserrat:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap');


html {
  overflow: hidden;
  align-self: center;
}

body, h1, h2, p, a {
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  height: 100%;
  background-color: #fff;
  color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.App-header {
  padding-left: 70px;
  padding-right: 70px;
  align-self: center;
  width: 100vw;
  height: 100vh;
  box-shadow: 0px 0px 17.6px 0px rgba(0,0,0,0.5);
  background-color: #fff;
  max-width: 1400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.profile-container {
  width: 388px;
  height: 388px;
  border-radius: 50%; 
  overflow: hidden; 
  margin-left: auto; /* Push the image container to the right */
  user-select: none;
  box-shadow: 10px 15px 20px rgba(0, 0, 0, 0.25);
}

.profile-image {
  height: 100%;
  width: auto;
  min-width: 100%;
  object-fit: cover;
  object-position: calc(50% + 30px) center; /* Slightly move the image to the left */
  user-select: none;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  margin-bottom: 2px;
  line-height: 1.2em;
  user-select: none;
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 85px;
  line-height: 100%;
  user-select: none;
}

h3 {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  line-height: 100%;
  color: #777777;
  user-select: none;
  transition: color 0.3s ease;
}

h3:hover {
  color: black;
}


h4 {
  font-family: 'Montserrat', sans-serif;
  color: #777777;
  font-size: 60px;
  line-height: 120%;
  user-select: none;
  transition: color 0.3s ease;
}

h4:hover {
  color: black;
}

h4.active {
  color: #000;
  font-size: 80px; 
}

.tagline {
  font-family: 'Cabin', sans-serif;
  font-size: 20px;
  line-height: 1.2em;
  margin-bottom: 2em;
  text-align: left;
  user-select: none;
}

.tagline {
  color: #777777;
}

.bold-text {
  color: #000000;
  font-weight: bolder;
}

.heading1 {
  @apply relative w-full text-center flex justify-center text-stone-300 text-7xl font-black font-['Montserrat'] leading-none z-0
}
/* Existing styles */

.navbar {
  align-self: center;
  padding-top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  max-width: 1400px;
  height: 78vh;
  position: fixed;
  background: #ffffff; /* Or any color you prefer */
  box-shadow: 0px 0px 30px rgba(0,0,0,0.5);
}

.tabs {
  height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
}

.hide-scrollbar::-webkit-scrollbar {
  display: none; /* For WebKit browsers */
}

.hide-scrollbar {
  -ms-overflow-style: none;  /* For Internet Explorer and Edge */
  scrollbar-width: none;  /* For Firefox */
}





@media (max-width: 768px) {
  .App-header {
    padding-left: 30px;
    padding-right: 20px;
  }
  
  .profile-container {
    width: 255px;
    height: 255px;
    border-radius: 50%; 
    overflow: hidden; 
    margin-left: auto; /* Push the image container to the right */
    margin-bottom: 50px;
  }
  
  .profile-image {
    height: 100%;
    width: auto;
    min-width: 100%;
    object-position: calc(50% + 20px) center; /* Slightly move the image to the left */
  }
  
  h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 32px;
    margin-bottom: 2px;
    line-height: 100%;
  }
  
  h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 55px;
    line-height: 100%;
  }

  h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    line-height: 100%;
  }
  
  .tagline {
    font-family: 'Cabin', sans-serif;
    font-size: 13px;
    margin-bottom: 2em;
    text-align: left;
  }
}
